.site-header{
    background-color: #1bbc9b;
    color: #ecfdf9;
    height: 100vh;
    font-family:monospace;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

body{
    margin:0;
}

.site-header-content{
    margin:10px;
}

.profile-pic{
    border-radius:50%;
    height: 200px;
    width: 200px;
    align-self: center;
    box-shadow: #1a5d4f 2px 5px 5px;
}